import React, { useState, useEffect } from 'react';
import { Waves, Volume2, RefreshCcw, AlertCircle } from 'lucide-react';
import { healthAPI } from '../services/api';

const BrowserSynthesisToggle = ({ onToggle, showError = false, errorMessage = null }) => {
  const [isEnabled, setIsEnabled] = useState(false);
  
  // Initialize from stored settings
  useEffect(() => {
    const enabled = healthAPI.isBrowserSynthesisEnabled();
    setIsEnabled(enabled);
  }, []);

  const handleToggle = () => {
    const result = healthAPI.toggleBrowserSynthesis();
    setIsEnabled(result.browserSynthesis);
    
    if (onToggle) {
      onToggle(result.browserSynthesis);
    }
  };

  return (
    <div className="p-4 bg-blue-50 border border-blue-200 rounded-lg">
      <div className="flex flex-col space-y-3">
        {showError && errorMessage && (
          <div className="flex items-start space-x-2 text-red-700 mb-2">
            <AlertCircle className="h-5 w-5 flex-shrink-0 mt-0.5" />
            <div>
              <p className="font-medium">{errorMessage}</p>
              <p className="text-sm mt-1">Try enabling browser synthesis mode below to resolve this issue.</p>
            </div>
          </div>
        )}
        
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-2">
            <Waves className={`h-5 w-5 ${isEnabled ? 'text-blue-600' : 'text-gray-500'}`} />
            <span className="font-medium">Browser Synthesis Mode</span>
          </div>
          
          <label className="relative inline-flex items-center cursor-pointer">
            <input
              type="checkbox"
              className="sr-only peer"
              checked={isEnabled}
              onChange={handleToggle}
            />
            <div className={`w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 rounded-full peer 
              ${isEnabled ? 'after:translate-x-full after:border-white bg-blue-600' : 'after:border-gray-300 bg-gray-200'}
              after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border after:rounded-full after:h-5 after:w-5 after:transition-all`}>
            </div>
            <span className="ml-3 text-sm font-medium text-gray-700">
              {isEnabled ? 'Enabled' : 'Disabled'}
            </span>
          </label>
        </div>
        
        <div className="text-sm text-gray-600 mt-2">
          {isEnabled ? (
            <p>
              <Volume2 className="h-4 w-4 inline mr-1 text-green-600" />
              Using browser's Tone.js synthesis engine to generate music directly in your browser.
              This provides enhanced audio quality and eliminates format compatibility issues.
            </p>
          ) : (
            <p>
              <RefreshCcw className="h-4 w-4 inline mr-1" />
              Using server-generated audio files. Enable browser synthesis if you experience
              audio playback issues or want to use the enhanced Tone.js synthesis engine.
            </p>
          )}
        </div>
        
        <div className="text-xs text-gray-500 mt-2 italic">
          {isEnabled && (
            <span>
              Powered by Tone.js: Advanced Web Audio framework for high-quality music synthesis
            </span>
          )}
        </div>
      </div>
    </div>
  );
};

export default BrowserSynthesisToggle;
