import axios from 'axios';
import AudioSynth from './audioSynth';

// API Configuration
// In development, use direct backend URL
// In production, use the configured backend URL
const isDevelopment = import.meta.env.DEV;
const PRODUCTION_BACKEND_URL = import.meta.env.VITE_API_URL && 
  import.meta.env.VITE_API_URL !== 'https://your-backend-url.com' ? 
  import.meta.env.VITE_API_URL : '';
const API_BASE_URL = isDevelopment ? 'http://localhost:5001' : PRODUCTION_BACKEND_URL;

// Demo mode flag - set to true when backend is not available
let isDemoMode = !API_BASE_URL; // Default to true if no backend URL

// Browser synthesis mode flag - can be enabled by user even when backend is available
let forceBrowserSynthesis = localStorage.getItem('forceBrowserSynthesis') === 'true';

// Log the API configuration for debugging
console.log(`API Configuration: ${isDevelopment ? 'Development' : 'Production'} mode`);
console.log(`API Base URL: ${API_BASE_URL || '(using relative URLs)'}`);
console.log(`Demo mode: ${isDemoMode ? 'Enabled' : 'Disabled'}`);
console.log(`Browser synthesis: ${forceBrowserSynthesis ? 'Enabled' : 'Disabled'}`);

// Create axios instance with default config
const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 30000, // 30 seconds for music generation
  headers: {
    'Content-Type': 'application/json',
  },
});

// Check if we're in demo mode (backend not available)
const checkDemoMode = async () => {
  // If no backend URL is configured, we're definitely in demo mode
  if (!API_BASE_URL) {
    console.log('No backend URL configured, enabling demo mode');
    isDemoMode = true;
    return;
  }
  
  if (isDevelopment) {
    try {
      await axios.get(`/health`, { timeout: 3000 });
      isDemoMode = false;
      console.log('Backend is available, using backend API');
    } catch (error) {
      console.warn('Development backend not available, enabling demo mode');
      isDemoMode = true;
    }
    return;
  }
  
  // In production, check if backend is available
  try {
    await axios.get(`${API_BASE_URL}/health`, { timeout: 3000 });
    isDemoMode = false;
    console.log('Production backend is available');
  } catch (error) {
    console.warn('Backend not available, enabling demo mode');
    isDemoMode = true;
  }
};

