// Health check and browser synthesis controls
export const healthAPI = {
  async checkHealth() {
    if (isDemoMode) {
      return { success: true, status: 'healthy', message: 'Demo mode active' };
    }
    
    try {
      // Use a direct axios call with shorter timeout to avoid blocking the UI for too long
      const url = isDevelopment ? '/health' : `${API_BASE_URL}/health`;
      const response = await axios.get(url, { timeout: 3000 });
      return response.data;
    } catch (error) {
      console.error('Health check failed:', error.message);
      // Return a structured error response instead of throwing
      return { 
        success: false, 
        status: 'unhealthy', 
        message: `Backend connection failed: ${error.message}`,
        error: true
      };
    }
  },
  
  // New method to force a health check and update demo mode
  async refreshBackendStatus() {
    try {
      await checkDemoMode();
      return { success: true, demoMode: isDemoMode };
    } catch (error) {
      console.error('Refresh backend status failed:', error.message);
      return { success: false, demoMode: true, error: error.message };
    }
  },
  
  // Browser synthesis mode control functions
  isBrowserSynthesisEnabled() {
    return forceBrowserSynthesis;
  },
  
  enableBrowserSynthesis() {
    forceBrowserSynthesis = true;
    localStorage.setItem('forceBrowserSynthesis', 'true');
    console.log('Browser synthesis mode enabled');
    return { success: true, browserSynthesis: true };
  },
  
  disableBrowserSynthesis() {
    forceBrowserSynthesis = false;
    localStorage.setItem('forceBrowserSynthesis', 'false');
    console.log('Browser synthesis mode disabled');
    return { success: true, browserSynthesis: false };
  },
  
  toggleBrowserSynthesis() {
    if (forceBrowserSynthesis) {
      return this.disableBrowserSynthesis();
    } else {
      return this.enableBrowserSynthesis();
    }
  }
};

// Initialize demo mode check with proper error handling
(async () => {
  try {
    await checkDemoMode();
    console.log(`Application initialized in ${isDemoMode ? 'demo' : 'backend'} mode`);
  } catch (error) {
    console.warn('Error during initialization check, defaulting to demo mode:', error.message);
    isDemoMode = true;
  }
})();

export default api;