/**
 * Enhanced Audio Player with Format Detection and Fallback
 * Provides comprehensive audio format support and error handling
 */

// Browser audio format support detection
const AudioFormatDetector = {
  // Check if browser supports various audio formats
  checkSupport() {
    const audio = document.createElement('audio');
    const formats = {
      mp3: !!(audio.canPlayType && audio.canPlayType('audio/mpeg').replace(/no/, '')),
      wav: !!(audio.canPlayType && audio.canPlayType('audio/wav').replace(/no/, '')),
      ogg: !!(audio.canPlayType && audio.canPlayType('audio/ogg').replace(/no/, '')),
      aac: !!(audio.canPlayType && audio.canPlayType('audio/aac').replace(/no/, '')),
      webm: !!(audio.canPlayType && audio.canPlayType('audio/webm').replace(/no/, '')),
      flac: !!(audio.canPlayType && audio.canPlayType('audio/flac').replace(/no/, ''))
    };
    
    console.log('Browser audio format support:', formats);
    return formats;
  },

  // Get the best supported format from available options
  getBestFormat(availableFormats = []) {
    const support = this.checkSupport();
    
    // Priority order: MP3 > WAV > OGG > AAC > WebM > FLAC
    const priority = ['mp3', 'wav', 'ogg', 'aac', 'webm', 'flac'];
    
    for (const format of priority) {
      if (support[format] && availableFormats.includes(format)) {
        return format;
      }
    }
    
    // If no specific format preference, return the first supported format
    for (const format of availableFormats) {
      if (support[format.toLowerCase()]) {
        return format;
      }
    }
    
    return null;
  },

  // Check if Web Audio API is available
  isWebAudioAvailable() {
    return !!(window.AudioContext || window.webkitAudioContext);
  }
};

// Enhanced Audio Player Class
class EnhancedAudioPlayer {
  constructor() {
    this.audio = null;
    this.audioContext = null;
    this.isPlaying = false;
    this.currentUrl = null;
    this.onPlayCallback = null;
    this.onPauseCallback = null;
    this.onErrorCallback = null;
    this.onEndCallback = null;
  }

  // Set event callbacks
  setCallbacks({ onPlay, onPause, onError, onEnd }) {
    this.onPlayCallback = onPlay;
    this.onPauseCallback = onPause;
    this.onErrorCallback = onError;
    this.onEndCallback = onEnd;
  }

  // Primary playback method with format detection
  async play(url, options = {}) {
    try {
      // Stop any current playback
      this.stop();

      // Try HTML5 Audio first
      const success = await this.playWithHtml5Audio(url, options);
      if (success) return true;

      // Fallback to Web Audio API
      if (AudioFormatDetector.isWebAudioAvailable()) {
        const webAudioSuccess = await this.playWithWebAudio(url, options);
        if (webAudioSuccess) return true;
      }

      // If all else fails, report error
      this.handleError('All playback methods failed. Browser may not support this audio format.');
      return false;

    } catch (error) {
      this.handleError('Audio playback failed: ' + error.message);
      return false;
    }
  }

  // HTML5 Audio playback
  async playWithHtml5Audio(url, options = {}) {
    return new Promise((resolve) => {
      try {
        this.audio = new Audio();
        let hasResolved = false;

        // Set up event listeners
        this.audio.onplay = () => {
          if (!hasResolved) {
            hasResolved = true;
            this.isPlaying = true;
            this.currentUrl = url;
            if (this.onPlayCallback) this.onPlayCallback();
            resolve(true);
          }
        };

        this.audio.onpause = () => {
          this.isPlaying = false;
          if (this.onPauseCallback) this.onPauseCallback();
        };

        this.audio.onended = () => {
          this.isPlaying = false;
          if (this.onEndCallback) this.onEndCallback();
        };

        this.audio.onerror = (e) => {
          if (!hasResolved) {
            hasResolved = true;
            console.warn('HTML5 Audio failed:', e);
            resolve(false);
          }
        };

        // Set audio properties
        this.audio.crossOrigin = 'anonymous';
        this.audio.preload = 'auto';
        
        // Add cache busting if needed
        const timestamp = Date.now();
        const audioUrl = url.includes('?') 
          ? `${url}&cacheBuster=${timestamp}` 
          : `${url}?cacheBuster=${timestamp}`;
        
        this.audio.src = audioUrl;

        // Attempt to play
        const playPromise = this.audio.play();
        if (playPromise !== undefined) {
          playPromise.catch((error) => {
            if (!hasResolved) {
              hasResolved = true;
              console.warn('HTML5 Audio play promise failed:', error);
              resolve(false);
            }
          });
        }

        // Timeout fallback
        setTimeout(() => {
          if (!hasResolved) {
            hasResolved = true;
            resolve(false);
          }
        }, 5000);

      } catch (error) {
        console.warn('HTML5 Audio setup failed:', error);
        resolve(false);
      }
    });
  }

  // Web Audio API playback
  async playWithWebAudio(url, options = {}) {
    try {
      // Create audio context
      const AudioContext = window.AudioContext || window.webkitAudioContext;
      this.audioContext = new AudioContext();

      // Fetch audio data
      const response = await fetch(url);
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const arrayBuffer = await response.arrayBuffer();
      const audioBuffer = await this.audioContext.decodeAudioData(arrayBuffer);

      // Create and connect audio nodes
      const source = this.audioContext.createBufferSource();
      source.buffer = audioBuffer;
      source.connect(this.audioContext.destination);

      // Set up event handlers
      source.onended = () => {
        this.isPlaying = false;
        if (this.onEndCallback) this.onEndCallback();
      };

      // Start playback
      source.start();
      this.isPlaying = true;
      this.currentUrl = url;

      if (this.onPlayCallback) this.onPlayCallback();

      console.log('Web Audio API playback started successfully');
      return true;

    } catch (error) {
      console.warn('Web Audio API playback failed:', error);
      return false;
    }
  }

  // Pause playback
  pause() {
    if (this.audio && this.isPlaying) {
      this.audio.pause();
      this.isPlaying = false;
    }
    if (this.audioContext) {
      this.audioContext.suspend();
      this.isPlaying = false;
    }
  }

  // Stop playback
  stop() {
    if (this.audio) {
      this.audio.pause();
      this.audio.currentTime = 0;
      this.audio.src = '';
      this.audio = null;
    }
    if (this.audioContext) {
      this.audioContext.close();
      this.audioContext = null;
    }
    this.isPlaying = false;
    this.currentUrl = null;
  }

  // Handle errors
  handleError(message) {
    console.error('EnhancedAudioPlayer error:', message);
    if (this.onErrorCallback) {
      this.onErrorCallback(message);
    }
  }

  // Get current playback state
  getState() {
    return {
      isPlaying: this.isPlaying,
      currentUrl: this.currentUrl,
      hasAudio: !!this.audio,
      hasWebAudio: !!this.audioContext
    };
  }
}

// Audio format conversion utilities
const AudioFormatConverter = {
  // Convert blob to different format (requires Web Audio API)
  async convertFormat(blob, targetFormat) {
    if (!AudioFormatDetector.isWebAudioAvailable()) {
      throw new Error('Web Audio API not available for format conversion');
    }

    try {
      const arrayBuffer = await blob.arrayBuffer();
      const AudioContext = window.AudioContext || window.webkitAudioContext;
      const audioContext = new AudioContext();
      const audioBuffer = await audioContext.decodeAudioData(arrayBuffer);

      // This is a simplified conversion - full conversion would require additional libraries
      return this.bufferToWav(audioBuffer);
    } catch (error) {
      throw new Error('Format conversion failed: ' + error.message);
    }
  },

  // Convert AudioBuffer to WAV format
  bufferToWav(buffer) {
    const length = buffer.length;
    const numberOfChannels = buffer.numberOfChannels;
    const sampleRate = buffer.sampleRate;
    const bytesPerSample = 2;
    const blockAlign = numberOfChannels * bytesPerSample;
    const byteRate = sampleRate * blockAlign;
    const dataSize = length * blockAlign;
    const bufferSize = 44 + dataSize;
    
    const arrayBuffer = new ArrayBuffer(bufferSize);
    const view = new DataView(arrayBuffer);
    
    // WAV header
    const writeString = (offset, string) => {
      for (let i = 0; i < string.length; i++) {
        view.setUint8(offset + i, string.charCodeAt(i));
      }
    };
    
    writeString(0, 'RIFF');
    view.setUint32(4, bufferSize - 8, true);
    writeString(8, 'WAVE');
    writeString(12, 'fmt ');
    view.setUint32(16, 16, true);
    view.setUint16(20, 1, true);
    view.setUint16(22, numberOfChannels, true);
    view.setUint32(24, sampleRate, true);
    view.setUint32(28, byteRate, true);
    view.setUint16(32, blockAlign, true);
    view.setUint16(34, 16, true);
    writeString(36, 'data');
    view.setUint32(40, dataSize, true);
    
    // Convert audio data
    let offset = 44;
    for (let i = 0; i < length; i++) {
      for (let channel = 0; channel < numberOfChannels; channel++) {
        const sample = Math.max(-1, Math.min(1, buffer.getChannelData(channel)[i]));
        view.setInt16(offset, sample < 0 ? sample * 0x8000 : sample * 0x7FFF, true);
        offset += 2;
      }
    }
    
    return new Blob([arrayBuffer], { type: 'audio/wav' });
  }
};

// Export the enhanced audio player
export default {
  AudioFormatDetector,
  EnhancedAudioPlayer,
  AudioFormatConverter
};
