import React, { useState, useEffect } from 'react'
import Header from './components/Header'
import Hero from './components/Hero'
import Features from './components/Features'
import MusicGenerator from './components/MusicGenerator'
import AdvancedMusicGenerator from './components/AdvancedMusicGenerator'
import Pricing from './components/Pricing'
import Footer from './components/Footer'
import DemoModeNotification from './components/DemoModeNotification'
import { healthAPI } from './services/api'

function App() {
  const [activeMode, setActiveMode] = useState('simple')
  const [isDemoMode, setIsDemoMode] = useState(false)
  const [isLoading, setIsLoading] = useState(true)

  // Check backend status on load
  useEffect(() => {
    checkBackendStatus();
  }, [])
  
  const checkBackendStatus = async () => {
    setIsLoading(true);
    try {
      const result = await healthAPI.refreshBackendStatus();
      setIsDemoMode(result.demoMode);
    } catch (error) {
      console.error('Failed to check backend status:', error);
      setIsDemoMode(true);
    } finally {
      setIsLoading(false);
    }
  }

  return (
    <div className="min-h-screen">
      {/* Demo Mode Notification Banner */}
      <DemoModeNotification isDemoMode={isDemoMode} onRetry={checkBackendStatus} />
      
      {isLoading ? (
        <div className="fixed inset-0 bg-white bg-opacity-75 flex items-center justify-center z-50">
          <div className="text-center p-4">
            <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500 mx-auto mb-3"></div>
            <p className="text-gray-700">Connecting to backend server...</p>
          </div>
        </div>
      ) : null}
      
      <Header />
      <Hero />
      <Features />
      
      {/* Mode Selector */}
      <div className="bg-gray-100 py-8" id="generator">
        <div className="max-w-4xl mx-auto px-4">
          <div className="flex justify-center space-x-4 mb-8">
            <button
              onClick={() => setActiveMode('simple')}
              className={`px-6 py-3 rounded-lg font-semibold transition-all ${
                activeMode === 'simple'
                  ? 'bg-blue-600 text-white shadow-lg'
                  : 'bg-white text-gray-700 hover:bg-gray-50'
              }`}
            >
              Simple Mode
            </button>
            <button
              onClick={() => setActiveMode('advanced')}
              className={`px-6 py-3 rounded-lg font-semibold transition-all ${
                activeMode === 'advanced'
                  ? 'bg-purple-600 text-white shadow-lg'
                  : 'bg-white text-gray-700 hover:bg-gray-50'
              }`}
            >
              Advanced Studio
            </button>
          </div>
          
          {/* Render active component */}
          {activeMode === 'simple' ? <MusicGenerator /> : <AdvancedMusicGenerator />}
        </div>
      </div>
      
      <Pricing />
      <Footer />
    </div>
  )
}

export default App