import React from 'react'
import ReactDOM from 'react-dom/client'
import App from './App.jsx'
import './index.css'
import { healthAPI } from './services/api'

// Initialize API connectivity in the background
healthAPI.refreshBackendStatus()
  .then(status => {
    console.log('Initial backend status check completed:', status);
  })
  .catch(error => {
    console.error('Failed to initialize API connectivity:', error);
  });

// Render the app
ReactDOM.createRoot(document.getElementById('root')).render(
  <App />
)